import { useEffect, useState } from "react";
import {
  GetTask,
  SendAttempt,
  SendSkippedAttempt,
} from "../../services/AlternativeTaskService";
import TaskFinished from "./TaskFinished";
// interface AlternativesTaskModel {
//   questions: AlternativesQuestionModel[];
// }

import {
  AlternativesQuestionModel,
  AlternativesAnswer,
  ATaskAttemptData,
} from "./Interfaces";

interface AlternativesTaskProps {
  setCurrentView: (view: view) => void;
  taskId: number;
  studentId: number;
}

interface view {
  view: string;
  taskId?: number;
}

function AlternativesTask({
  setCurrentView,
  taskId,
  studentId,
}: AlternativesTaskProps) {
  const [isLoading, setIsLoading] = useState(true);
  const [isFirstRound, setIsFirstRound] = useState(true);
  const [taskFinishedView, setTaskFinishedView] = useState(false);

  const [pickedAnswer, setPickedAnswer] = useState<AlternativesAnswer | null>(
    null
  );

  const [currentQuestionIndex, setCurrentQuestionIndex] = useState(0);
  const [fetchedQuestions, setFetchedQuestions] = useState<
    AlternativesQuestionModel[]
  >([
    {
      question: {
        text: "Cual de todas las alternativas representa el espectro audible:",
      },
      answers: [
        {
          text: "400 Hz < A < 8000 Hz",
          isCorrect: true,
          incorrectHint: "La frecuencia fundamental de la cuerda aumenta",
        },
        {
          text: "3000 Hz < A < 15000 Hz",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda disminuye",
        },
        {
          text: "20 Hz < A < 20000 Hz",
          isCorrect: false,
          incorrectHint:
            "La frecuencia fundamental de la cuerda se mantiene igual",
        },
      ],
      savedPickedAnswer: null,
    },
    {
      question: {
        text: "Esta pregunta es A",
      },
      answers: [
        {
          text: "A",
          isCorrect: true,
          incorrectHint: "La frecuencia fundamental de la cuerda aumenta",
        },
        {
          text: "V",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda disminuye",
        },
        {
          text: "C",
          isCorrect: false,
          incorrectHint:
            "La frecuencia fundamental de la cuerda se mantiene igual",
        },
      ],
      savedPickedAnswer: null,
    },
    {
      question: {
        text: "Esta pregunta es B",
      },
      answers: [
        {
          text: "A",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda aumenta",
        },
        {
          text: "B",
          isCorrect: true,
          incorrectHint: "La frecuencia fundamental de la cuerda disminuye",
        },
        {
          text: "C",
          isCorrect: false,
          incorrectHint:
            "La frecuencia fundamental de la cuerda se mantiene igual",
        },
      ],
      savedPickedAnswer: null,
    },
    {
      question: {
        text: "Esta pregunta es C",
      },
      answers: [
        {
          text: "A",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda aumenta",
        },
        {
          text: "B",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda disminuye",
        },
        {
          text: "C",
          isCorrect: true,
          incorrectHint:
            "La frecuencia fundamental de la cuerda se mantiene igual",
        },
      ],
      savedPickedAnswer: null,
    },
    {
      question: {
        text: "Esta pregunta es D",
      },
      answers: [
        {
          text: "A",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda aumenta",
        },
        {
          text: "V",
          isCorrect: false,
          incorrectHint: "La frecuencia fundamental de la cuerda disminuye",
        },
        {
          text: "D",
          isCorrect: true,
          incorrectHint:
            "La frecuencia fundamental de la cuerda se mantiene igual",
        },
      ],
      savedPickedAnswer: null,
    },
  ]);

  const [secondRoundQuestions, setSecondRoundQuestions] = useState<
    AlternativesQuestionModel[]
  >([]);

  // useEffect for fetching the task from the server
  useEffect(() => {
    // Tell server to generate Task -> Fetch the task from the server
    // Get the task from the server
    console.log(`Fetching Alternatives Task with id ${taskId}`);
    GetTask(taskId)
      .then((fetchedTask) => {
        console.log("Fetched task: ", fetchedTask);
        setFetchedQuestions(fetchedTask.questions);
        setIsLoading(false);
      })
      .catch((error) => {
        console.log(`Error fetching Task ${error}`);
        setIsLoading(false);
      });
    // Set the task in the state
  }, [isLoading, taskId]);

  // useEffect for the next button
  useEffect(() => {
    // Check if the user is in the first round or the second round
    if (isFirstRound && !taskFinishedView) {
      // If the user is in the first round and the current question index is 4, make the button say "Finish Task"
      if (currentQuestionIndex === fetchedQuestions.length - 1) {
        document.getElementById("next-btn")!.innerHTML = "Terminar Tarea";
      }
      // If the user is in the first round and the current question index is not 4, make the button say "Next"
      else {
        document.getElementById("next-btn")!.innerHTML = "Siguiente";
      }
    } else {
      // If the user is in the second round and the current question index is the last one, make the button say "Terminar Tarea"
      if (!taskFinishedView) {
        if (currentQuestionIndex === secondRoundQuestions.length - 1) {
          document.getElementById("next-btn")!.innerHTML = "Terminar Tarea";
        }
        // If the user is in the second round and the current question index is not the last one, make the button say "Next"
        else {
          document.getElementById("next-btn")!.innerHTML = "Siguiente";
        }
      }
    }
  }, [
    currentQuestionIndex,
    isFirstRound,
    secondRoundQuestions,
    fetchedQuestions,
    taskFinishedView,
  ]);

  const handleSkipTest = () => {
    console.log("Skipping task");
    document.getElementById("skip-btn")?.classList.add("animate-bounce");
    // Send a skipped task attempt to the server
    SendSkippedAttempt(studentId, taskId)
      .then((data) => {
        console.log(data);
        console.log("Task skipped succesfully");
        setCurrentView({ view: "home", taskId: undefined });
      })
      .catch((error) => {
        console.log(`Error skipping task ${error}`);
      });
  };

  const handleNextQuestionClick = () => {
    // 1. Check that the user picked an answer
    if (!pickedAnswer) {
      // Animate next button to show that the user needs to pick an answer
      console.log("No answer picked");

      document.getElementById("next-btn")?.classList.add("animate-wiggle");
      document.getElementById("next-btn")?.classList.add("bg-red-500");
      setTimeout(() => {
        document.getElementById("next-btn")?.classList.remove("animate-wiggle");
        document.getElementById("next-btn")?.classList.remove("bg-red-500");
        // console.log("Wiggle removed")
      }, 500);

      return;
    }
    // console.log("Answer picked")
    let updatedQuestions = [...fetchedQuestions];

    // 2. Save the picked answer in the fetchedQuestions state
    if (isFirstRound) {
      updatedQuestions = fetchedQuestions.map((question, index) => {
        if (index === currentQuestionIndex) {
          return {
            ...question,
            savedPickedAnswer: pickedAnswer,
          };
        } else {
          return question;
        }
      });

      if (currentQuestionIndex === fetchedQuestions.length -1) {
        const lastQuestion = updatedQuestions[currentQuestionIndex];
        if (lastQuestion.savedPickedAnswer === null) {
          // If not, set a default answer (you can modify this as needed)
          updatedQuestions[currentQuestionIndex] = {
            ...lastQuestion,
            savedPickedAnswer: {
              text: "No answer",
              isCorrect: false,
              incorrectHint: "You didn't select an answer.",
            },
          };
        }
      }

    } else {
      const currentQuestion = secondRoundQuestions[currentQuestionIndex];

      const matchingIndex = updatedQuestions.findIndex(
        (question) => question.question.text === currentQuestion.question.text
      );

      if (matchingIndex !== -1) {
        updatedQuestions[matchingIndex] = {
          ...updatedQuestions[matchingIndex],
          savedPickedAnswer: pickedAnswer,
        };
      }
    }

    // 3. Update the fetchedQuestions state

    console.log("New fetched questions: ", updatedQuestions);
    setFetchedQuestions(prev => updatedQuestions);
    console.log("Second round questions: ", secondRoundQuestions);

    // 4. Animate the next question button

    document.getElementById("next-btn")?.classList.add("bg-green-500");
    document.getElementById("next-btn")?.classList.add("animate-ping");
    setTimeout(() => {
      document.getElementById("next-btn")?.classList.remove("bg-green-500");
      document.getElementById("next-btn")?.classList.remove("animate-ping");
      // console.log("Wiggle removed")
    }, 500);

    // 5. Check if the user is in the first round or the second round
    if (isFirstRound) {
      // If the question answer is incorrect, add the question to the second round questions
      if (!pickedAnswer.isCorrect) {
        // Create a deep copy of the question object
        const copiedQuestion = JSON.parse(
          JSON.stringify(fetchedQuestions[currentQuestionIndex])
        );

        // Modify the copied question's `pickedQuestion` attribute
        copiedQuestion.savedPickedAnswer = pickedAnswer;

        // Add the copied question to the second round questions array
        setSecondRoundQuestions([...secondRoundQuestions, copiedQuestion]);
      }

      // If the user is in the first round and the current question index is 4, set the state to second round
      if (currentQuestionIndex === fetchedQuestions.length - 1) {
        if (secondRoundQuestions.length === 0) {
          // If theres no wrong answers
          finishTask(updatedQuestions);
        } else {
          // If there's wrong answers, go to the second round
          setIsFirstRound(false);
          // Set the current question index to 0
          setCurrentQuestionIndex(0);
        }
      }
      // If the user is in the first round and the current question index is not 4, increment the current question index
      else {
        setCurrentQuestionIndex(currentQuestionIndex + 1);
      }
    } else {
      // If there are no more questions in the second round, finish the task
      if (secondRoundQuestions.length === 0) {
        finishTask(updatedQuestions);
      }
      // If the user is in the second round and the current question index is the last one, finish the task
      if (currentQuestionIndex === secondRoundQuestions.length - 1) {
        finishTask(updatedQuestions);
      }
      // If the user is in the second round and the current question index is not the last one, increment the current question index
      else {
        setCurrentQuestionIndex(currentQuestionIndex + 1);
      }
    }
    // 3. Set the picked answer to null

    setPickedAnswer(null);
  };

  const finishTask = (questionsToProcess: AlternativesQuestionModel[]) => {
    console.log("Task finished");
    // TODO Give the send-btn a loading animation
    // Create a loading spinner
    // Append child to the button

    document.getElementById("next-btn")!.innerHTML = "Procesando...";
    document.getElementById("next-btn")!.classList.add("animate-bounce");

    // Prepare the task data to be sent to the server
    // Check each that each question in fetchedQuestions has an answer and isCorrect attribute

    const fetchedQuestionsData: ATaskAttemptData[] = questionsToProcess.map(
      (question) => ({
        text: question.question.text,
        savedPickedAnswer: {
          text: question.savedPickedAnswer
            ? question.savedPickedAnswer.text
            : "",
          isCorrect: question.savedPickedAnswer
            ? question.savedPickedAnswer.isCorrect
            : false,
          incorrectHint: question.savedPickedAnswer
            ? question.savedPickedAnswer.incorrectHint
            : "",
        },
      })
    );
    // Send the task to the server
    console.log("Sending task to server");
    console.log("AttemptData", fetchedQuestionsData);

    SendAttempt(studentId, taskId, fetchedQuestionsData)
      .then((data) => {
        console.log(data);
        console.log("Task sent succesfully");
        setTaskFinishedView(true);
      })
      .catch((error) => {
        console.log(`Error sending task ${error}`);
        document.getElementById("next-btn")!.innerHTML = "Terminar Tarea";
        document.getElementById("next-btn")!.classList.remove("animate-bounce");
      });
    // Get the task score from the server

    // Set the task finished data in the state based on the task score from the server

    // Set the current view to task finished view

    // Reset the task
  };

  return (
    <>
      {taskFinishedView ? (
        <TaskFinished
          sentTask={fetchedQuestions}
          setCurrentView={setCurrentView}
        />
      ) : (
        <div className="h-screen">
          <header className="w-full h-[15%] bg-green-200 flex flex-row-reverse px-28 content-center">
            <button
              className="bg-orange-400 h-min self-center px-4 py-2 rounded-md text-white font-bold"
              onClick={handleSkipTest}
              id="skip-btn"
            >
              Saltar Tarea
            </button>
          </header>

          {isLoading ? (
            <div className="flex justify-center items-center">
              <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
            </div>
          ) : (
            <div className="h-[65%]">
              <section className="flex gap-x-8 content-center px-20 h-[20%] bg-green-200 place-items-center cursor-default">
                <figure className="text-5xl bg-gray-200 p-4 rounded-md">
                  {currentQuestionIndex + 1}
                </figure>
                <h2 className="text-2xl">
                  {isFirstRound
                    ? fetchedQuestions[currentQuestionIndex].question.text
                    : secondRoundQuestions[currentQuestionIndex] &&
                      secondRoundQuestions[currentQuestionIndex].question.text}
                </h2>
              </section>
              <section className="bg-slate-500 h-[80%] flex gap-5 content-center justify-around px-[15%]">
                {/* Answers go here */}
                <AnswerButton
                  answer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].answers[0]
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex].answers[0]
                  }
                  setPickedAnswer={setPickedAnswer}
                  isFirstRound={isFirstRound}
                  savedPickedAnswer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].savedPickedAnswer
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex]
                          .savedPickedAnswer
                  }
                />
                <AnswerButton
                  answer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].answers[1]
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex].answers[1]
                  }
                  setPickedAnswer={setPickedAnswer}
                  isFirstRound={isFirstRound}
                  savedPickedAnswer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].savedPickedAnswer
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex]
                          .savedPickedAnswer
                  }
                />
                <AnswerButton
                  answer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].answers[2]
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex].answers[2]
                  }
                  setPickedAnswer={setPickedAnswer}
                  isFirstRound={isFirstRound}
                  savedPickedAnswer={
                    isFirstRound
                      ? fetchedQuestions[currentQuestionIndex].savedPickedAnswer
                      : secondRoundQuestions[currentQuestionIndex] &&
                        secondRoundQuestions[currentQuestionIndex]
                          .savedPickedAnswer
                  }
                />
              </section>
            </div>
          )}
          <footer className="h-[15%] bg-green-200 grid grid-cols-4">
            <div className="col-span-3 bg-green-200 flex gap-10 px-16">
              {/* Bar with test progress */}
              <div className="bg-gray-100 h-[8%] my-auto align-self-center w-3/4 relative flex rounded-lg justify-between">
                {/* As many dots as questions */}
                {fetchedQuestions &&
                  fetchedQuestions.map((question, index) => (
                    <figure
                      key={index}
                      className="rounded-full h-full w-[1%] border-[1.5px] p-2 self-center bg-white border-black z-10"
                    ></figure>
                  ))}
                <div
                  style={{
                    width: `${
                      (currentQuestionIndex / (fetchedQuestions.length - 1)) *
                      100
                    }%`,
                  }}
                  className="absolute bottom-0 left-0 progress-bar bg-green-300 rounded-l-lg h-full"
                ></div>
              </div>

              <h2 className="my-auto align-self-center text-lg">
                {currentQuestionIndex + 1}/
                {isFirstRound
                  ? fetchedQuestions.length
                  : secondRoundQuestions.length}
              </h2>
            </div>
            <div className="col-span-1 flex flex-row-reverse gap-8 px-28 content-center">
              <button
                id="next-btn"
                className="bg-orange-400 h-min min-w-min self-center px-4 py-2 w-1/2 rounded-md text-white font-bold"
                onClick={handleNextQuestionClick}
              >
                Siguiente
              </button>
            </div>
          </footer>
          <nav className="h-[5%] bg-green-200 text-center align-text-bottom">
            ______
          </nav>
        </div>
      )}
    </>
  );
}

interface AnswerButtonProps {
  answer: AlternativesAnswer;
  setPickedAnswer: (answer: AlternativesAnswer) => void;
  isFirstRound: boolean;
  savedPickedAnswer?: AlternativesAnswer | null;
}

function AnswerButton({
  answer,
  setPickedAnswer,
  isFirstRound,
  savedPickedAnswer,
}: AnswerButtonProps) {
  // Check if this answer is the saved picked answer and if it's incorrect
  const isSavedIncorrectAnswer =
    savedPickedAnswer &&
    savedPickedAnswer.text === answer.text &&
    !savedPickedAnswer.isCorrect;

  const handleClick = () => {
    // Set the current answer in the state
    setPickedAnswer(answer);
    // console.log(answer);
  };

  return (
    <div className="grid grid-rows-3 gap-5 max-w-[33%] place-content-center">
      {/* If its the second round and the picked answer was incorrect (the savedPickedAnswer exists and it's equal to this answer), show incorrect hint */}
      {answer && isSavedIncorrectAnswer ? (
        <div className="self-center row-span-3 flex flex-col">
          <button className="bg-red-500 px-4 py-6 rounded-md self-center cursor-default">
            {answer.text}
          </button>
          <figcaption className="border-2 border-red-600 px-2  pt-2 pb-8 mb-6 text-center self-center cursor-default ">
            <h3 className="text-red-600 font-bold pb-1">
              Porque es incorrecto?
            </h3>
            <p className="font-medium">{answer.incorrectHint}</p>
          </figcaption>
        </div>
      ) : (
        <button
          className="bg-sky-500 min-h-[35%] hover:bg-sky-700 px-4 py-6 rounded-md row-span-3 self-center active:bg-sky-500 focus:outline-none focus:ring focus:ring-blue-300"
          onClick={handleClick}
        >
          {answer && answer.text}
        </button>
      )}
    </div>
  );
}

export default AlternativesTask;
