import configData from "../config.json";


export async function GetAllStudents(): Promise<any> {
    return fetch(`${configData.SERVER_URL}/students/?format=json`,
    {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
        },
    }
    ).then((response)=> response.json())
    .then((data) => {
        console.log(data);
        return data;
    })
    .catch((error) => {
        console.log(error);
        throw error;
    })
}

export async function GetId(username: string): Promise<number> {
    return fetch(`${configData.SERVER_URL}/students/?format=json`,
    {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
        },
    }
    ).then((response)=> response.json())
    .then((data) => {
        console.log(data);
        const student = data.filter((student: any) => student.name === username);
        const id = student[0].id;
        return id;
    })
    .catch((error) => {
        console.log(error);
        throw error;
    })
}

