export async function CreateStudent(username: string): Promise<any> {
    return fetch(`${configData.SERVER_URL}/students/`, {
        method: "POST",
        headers: {
        "Content-Type": "application/json",
        },
        body: JSON.stringify({ name: username }),
    })
        .then((response) => response.json())
        .then((data) => {
        console.log(data);
        return data;
        })
        .catch((error) => {
        console.log(error);
        throw error;
        });
    }
