import 'chart.js/auto';
import { Bar, Doughnut } from 'react-chartjs-2';
import { useState, useEffect } from 'react';
import { GetMainDashboard } from '../../services/DashboardService';
import { GetTask as GetAlternativeTask } from '../../services/AlternativeTaskService';
import { GetTask as GetCalculusTask } from '../../services/CalculusTaskService';

interface View {
  view: string;
  taskId?: number;
}

interface MainDashboardProps {
  setCurrentView: (view: View) => void;
}

interface UnlockedConceptsData {
  concept: string;
  studentsUnlocked: number;
}

interface JsonData {
  unlocked_concepts: Record<string, number>;
  highlighted_task: [number, string];
  total_app_time: number;
  score_percentage_intervals_per_concept: Record<
    string,
    Record<string, number>
  >;
}

interface ConceptData {
  id: number;
  name: string;
}

function MainDashboard({ setCurrentView }: MainDashboardProps) {
  const [fetchedData, setFetchedData] = useState<JsonData | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [concepts, setConcepts] = useState<ConceptData[]>([
    {
      id: 1,
      name: 'Tipos de ondas',
    },
    {
      id: 2,
      name: 'Características de las ondas',
    },
    {
      id: 3,
      name: 'Ondas estacionarias y viajeras',
    },
    {
      id: 4,
      name: 'Ondas sonoras',
    },
    {
      id: 5,
      name: 'Movimiento de una cuerda',
    },
  ]);

  useEffect(() => {
    GetMainDashboard().then((data) => {
      setFetchedData(data);
      setIsLoading(false);
    });
  }, [isLoading]);

  const handleGoBack = () => {
    setCurrentView({ view: 'teacherHome' });
  };

  return (
    <div className="h-screen">
      <header className="w-full h-[10%] bg-blue-200 grid grid-cols-5 gap-10 justify-center px-28 content-center">
        <div className="px-2 py-2 col-start-1 col-end-4 text-right  text-5xl">
          Dashboard General
        </div>
        <button
          type="button"
          className="bg-slate-400 h-min col-start-5 col-end-5 px-4 py-2 rounded-md text-white font-bold text-4xl"
          onClick={handleGoBack}
        >
          🏠
        </button>
      </header>

      <section className="h-[85%] grid grid-rows-2 grid-cols-2 justify-items-center items-center">
        { isLoading ? (
          <div className="flex justify-center items-center h-[80%]">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
        </div>
        ) : (
          <>
          <article className="h-[75%] w-[75%] bg-gray-300 shadow-lg">
            {fetchedData && (
              <UnlockedConceptsBarChart
                unlockedConceptsData={Object.entries(
                  fetchedData.unlocked_concepts
                ).map(([concept, studentsUnlocked]) => ({
                  concept,
                  studentsUnlocked,
                }))}
                concepts={concepts}
              />
            )}
          </article>
          <article className="h-[75%] w-[75%] bg-gray-300 shadow-lg flex flex-col items-center justify-center">
                <h2 className="text-xl">Tiempo Total Usado en la App</h2>
                <div className="h-[80%] w-[35%] rounded-full flex flex-col gap-1 items-center justify-center border-4 border-cyan-500 ">
                  <h4 className="text-6xl font-bold">{fetchedData && fetchedData.total_app_time}</h4>
                  <h5 className="">segundos</h5>
                </div>
  
          </article>
          <article className="h-[90%] w-[90%]  bg-gray-300 shadow-lg">
            <h2 className="h-[10%] text-center my-2 text-xl">Puntajes Promedio por Concepto</h2>
          <div className="h-[90%] grid grid-cols-5 justify-items-evenly overflow-x-auto">

          {/* Render IntervalScorePieChart for each concept */}
          {fetchedData &&
              concepts.map((concept) => (
                <div key={concept.id} className="flex flex-col col-span-1 h-full ">
                  <IntervalScorePieChart
                    intervalScoreData={fetchedData.score_percentage_intervals_per_concept[concept.id.toString()]}
                    />
                  <h2 className="text-xs text-center h-[5%]">{concept.name}</h2>
                </div>
              ))}
              </div>
          </article>
          <article className="h-[90%] w-[90%] bg-gray-300 shadow-lg">
            <div className="flex flex-col items-center justify-center">
              <h2 className="text-xl my-2">Tarea Destacada (Con menor % de aciertos) </h2>
                { fetchedData && fetchedData.highlighted_task && fetchedData.highlighted_task[1] === 'Alternative' ? (
  
                  <MiniAlternativeTask id={fetchedData.highlighted_task[0]} />
                  
                  ) : fetchedData && fetchedData.highlighted_task && fetchedData.highlighted_task[1] === 'Calculus' ? (
                    <MiniCalculusTask id={fetchedData.highlighted_task[0]} />
                    ) : ( 
                      <></>
                      )
                    }
                    </div>
          </article>
          </>

          )
        }
      </section>
      <nav className="h-[5%] bg-blue-200 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

function UnlockedConceptsBarChart({
  unlockedConceptsData,
  concepts,
}: {
  unlockedConceptsData: UnlockedConceptsData[];
  concepts: ConceptData[];
}) {
  const labels = unlockedConceptsData.map((data) => {
    const concept = concepts.find((c) => c.id === parseInt(data.concept));
    return concept ? concept.name : '';
  });

  const chartData = {
    labels: labels,
    datasets: [
      {
        label: 'Estudiantes',
        data: unlockedConceptsData.map((data) => data.studentsUnlocked),
        backgroundColor: 'rgba(54, 162, 235, 0.6)', 
      },
    ],
  };

  const chartOptions = {
    plugins: {
      title: {
        display: true,
        text: 'Conceptos desbloqueados por alumno',
        fontSize: 16,
      },
    },
    scales: {
      y: {
        beginAtZero: true,
        title: {
          display: true,
          text: 'Numero de Estudiantes',
        },
      },
    },
  };

  return (
    <div className="chart-container">
      <Bar data={chartData} options={chartOptions} />
    </div>
  );
}

interface IntervalScoreData {
  [interval: string]: number;
}
interface IntervalScorePieChartProps {
  intervalScoreData: IntervalScoreData;
}

function IntervalScorePieChart({
  intervalScoreData,
}: IntervalScorePieChartProps) {
  const chartData = {
    labels: Object.keys(intervalScoreData),
    datasets: [
      {
        data: Object.values(intervalScoreData),
        backgroundColor: [
          'rgba(255, 0, 0, 0.6)',
          'rgba(255, 255, 0, 0.6)',
          'rgba(0, 255, 0, 0.6)',
        ],
      },
    ],
  };

  const chartOptions = {
    responsive: true,
    maintainAspectRatio: false,
    plugins: {
      title: {
        display: false,
        text: 'Average Score Intervals by Concept',
        fontSize: 16,
      },
    },
    legend: {
      display: false,
      position: 'top',
      labels: {
        fontSize: 9,
      },
    },
  };

  return (
    <div className="w-[100%] h-fit max-h-[95%]" >
      <Doughnut data={chartData} options={chartOptions} />
    </div>
  );
}

interface CalculusTaskModel {
  imageUrl: string;
  question: string;
  answers: CalculusTaskAnswer[];
}

interface CalculusTaskAnswer {
  variableName: string;
  value: number | null;
  correctValue: number;
  unit: string | null;
  correctUnit: string;
}

interface MiniCalculusTaskProps {
  id: number;
}

function MiniCalculusTask({ id }: MiniCalculusTaskProps) {
  const [fetchedTasks, setFetchedTasks] = useState<CalculusTaskModel[]>([]);
  const [calculusTask, setCalculusTask] = useState<CalculusTaskModel>({
    imageUrl: "https://picsum.photos/500/500",
    question: "What is the area under the curve?",
    answers: [
      {
        variableName: "a",
        correctValue: 10,
        value: null,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "b",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "c",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "d",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
    ],
  });
  const [isLoading  , setIsLoading] = useState(true);

  useEffect(() => {
    GetCalculusTask(id).then((fetchedTask) => {
      setCalculusTask(fetchedTask);
      setIsLoading(false);
    });
  }, [isLoading, id]);

  return (
    <div className="grid grid-cols-2">
      <section className="text-sm col-span-1 text-center">{calculusTask.question}</section>
      <figure className="col-span-1 xl:p-4 grid justify-center content-center">
      <img
                src={calculusTask.imageUrl}
                alt="Task Diagram"
                className=" h-[100%] w-[100%]"
              />
      </figure>
      
    </div>
  );


}


interface AlternativesQuestionModel {
  question: AlternativesQuestion;
  answers: AlternativesAnswer[];
  savedPickedAnswer: AlternativesAnswer | null;
}

interface AlternativesQuestion {
  text: string;
}

interface AlternativesAnswer {
  text: string;
  isCorrect: boolean;
  incorrectHint: string;
}

interface MiniAlternativeTaskProps {
  id: number;
}

function MiniAlternativeTask( {id}: MiniAlternativeTaskProps) {
  const [fetchedQuestions, setFetchedQuestions] = useState<AlternativesQuestionModel[]>([]);

  useEffect(() => {
    GetAlternativeTask(id).then((data) => {
      setFetchedQuestions(data.questions);
    }
    );
  }, [id])

  return (
    <div className="flex gap-1 justify-evenly items-center ">
      {fetchedQuestions.map((question) => (
        <MiniAlternativeQuestion key={question.question.text} question={question} />
        
      ))}
    </div>
  )

}

interface MiniAlternativeQuestionProps {
  question: AlternativesQuestionModel;
}
function MiniAlternativeQuestion( {question}: MiniAlternativeQuestionProps) {
  return (
    <div className="min-w-[15%] max-w-[20%] md:text-[.5rem] 2xl:text-xs p-2 bg-gray-200 border-slate-500 rounded-md">
      <p className=" text-center">{question.question.text}</p>
      <div className="flex flex-col gap-1">
        {question.answers.map((answer) => (
          <MiniAlternativeAnswer key={answer.text} answer={answer} />
        ))}
      </div>

    </div>
  )
}

interface MiniAlternativeAnswerProps {
  answer: AlternativesAnswer;
}

function MiniAlternativeAnswer( {answer}: MiniAlternativeAnswerProps) {
  const shortenAnswer = (answer : string) => {
    if (answer.length > 45) {
      return answer.substring(0, 45) + "...";
    }
    return answer;
  }
  return (
    <div className="bg-blue-600 text-white rounded-md text-center py-2 px-1">
      <p>{shortenAnswer(answer.text)}</p>
    </div>
  )
}

export default MainDashboard;
