import { useState, useEffect } from "react";
import { GetTask, SendSkippedAttempt } from "../../services/CalculusTaskService";
import CalculusInterstice from "./CalculusInterstice";
interface CalculusTaskProps {
  setCurrentView: (view: view) => void;
  taskId: number;
  studentId: number;
}

interface view {
  view: string;
  taskId?: number;
}

interface CalculusTaskModel {
  imageUrl: string;
  question: string;
  answers: CalculusTaskAnswer[];
}

interface CalculusTaskAnswer {
  variableName: string;
  value: number | null;
  correctValue: number;
  unit: string | null;
  correctUnit: string;
}

function CalculusTask({
  setCurrentView,
  taskId,
  studentId,
}: CalculusTaskProps) {
  const [isLoading, setIsLoading] = useState(true);

  const [isFirstRound, setIsFirstRound] = useState(true);
  const [inInterstice, setInInterstice] = useState(false);
  const [incorrectAnswers, setIncorrectAnswers] = useState<
    CalculusTaskAnswer[]
  >([]);
  const [correctAnswers, setCorrectanswers] = useState<CalculusTaskAnswer[]>(
    []
  );
  const [calculusTask, setCalculusTask] = useState<CalculusTaskModel>({
    imageUrl: "https://picsum.photos/500/500",
    question: "What is the area under the curve?",
    answers: [
      {
        variableName: "a",
        correctValue: 10,
        value: null,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "b",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "c",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "d",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
    ],
  });
  const [currentAnswers, setCurrentAnswers] = useState<CalculusTaskAnswer[]>(
    calculusTask.answers
  );

  useEffect(() => {
    // Tell server to generate Task -> Fetch the task from the server
    // Get the task from the server
    console.log(`Fetching Calculus Task with id ${taskId}`);

    GetTask(taskId)
      .then((fetchedTask) => {
        console.log("Fetched task: ", fetchedTask);
        setCalculusTask(fetchedTask);
        setCurrentAnswers(fetchedTask.answers);
        setIsLoading(false);
      })
      .catch((error) => {
        console.log(`Error fetching task: ${error}`);
        setIsLoading(false);
      });

    // Set the task in the state
  }, [isLoading, taskId]);

  // useEffect for task correctness

  const handleSkipTest = () => {
    document.getElementById("skip-btn")?.classList.add("animate-bounce");
    // Send the skipped attempt to the server
    SendSkippedAttempt(studentId, taskId)
      .then((response) => {
        console.log(response);
        setCurrentView({view: "home", taskId : undefined});
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const checkIfAllAnswersAreFilled = () => {
    // Check if all the answers are filled
    let allAnswersAreFilled = true;
    currentAnswers.forEach((answer) => {
      if (answer.value === null || answer.unit === null) {
        allAnswersAreFilled = false;
      }
    });
    return allAnswersAreFilled;
  };

  const checkIfAnyAnswerIsIncorrect = () => {
    const tempIncorrectAnswers: CalculusTaskAnswer[] = [];
    const tempCorrectAnswers: CalculusTaskAnswer[] = [];
    // Check if any answer is incorrect
    currentAnswers.forEach((answer) => {
      if (answer.value != null && answer.unit != null) {
        // For each question, check if the answer exceeds our margin of error
        // Then classify it as correct or incorrect
        const incorrectPercentage = Math.floor(
          (Math.abs(answer.correctValue - answer.value) / answer.correctValue) *
            100
        );
        if (incorrectPercentage > 5) {
          tempIncorrectAnswers.push(answer);
        } else {
          tempCorrectAnswers.push(answer);
        }
      }
    });
    // Update states
    setCorrectanswers(tempCorrectAnswers);
    setIncorrectAnswers(tempIncorrectAnswers);

    // Check if there's any incorrect answers
    if (tempIncorrectAnswers.length > 0) {
      return true;
    }
    return false;
  };

  const handleSendTaskClick = () => {
    // 1. Check that the user filled out all the answers
    if (!checkIfAllAnswersAreFilled()) {
      // Animate the finish task button to show that the user needs to fill out all answers
      console.log("You are missing answers to fill out");

      document.getElementById("send-btn")?.classList.add("animate-wiggle");
      document.getElementById("send-btn")?.classList.add("bg-red-500");

      setTimeout(() => {
        document.getElementById("send-btn")?.classList.remove("animate-wiggle");
        document.getElementById("send-btn")?.classList.remove("bg-red-500");
      }, 500);

      return;
    } else {
      // 2. Check if the user got all the answers correct
      checkIfAnyAnswerIsIncorrect();
      setInInterstice(true);
    }
  };



  return (
    <div className="h-screen">
      <header className="w-full h-[15%] bg-blue-400 flex flex-row-reverse px-28 content-center">
        <button
          className="bg-orange-400 h-min self-center px-4 py-2 w-[10%] rounded-md text-white font-bold"
          onClick={handleSkipTest}
          id="skip-btn"
        >
          Saltar Tarea
        </button>

        {inInterstice ? (
          <h2 className="text-6xl font-semibold  w-[90%] text-center self-center">
            Resultados{" "}
          </h2>
        ) : (
          <></>
        )}
      </header>

      {isLoading ? (
        <div className="flex justify-center items-center h-[80%]">
          <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
        </div>
      ) : inInterstice ? (
        <CalculusInterstice
          incorrectAnswers={incorrectAnswers}
          correctAnswers={correctAnswers}
          setIsFirstRound={setIsFirstRound}
          isFirstRound={isFirstRound}
          setInterstice={setInInterstice}
          setCurrentView={setCurrentView}
          studentId={studentId}
          taskId={taskId}
        />
      ) : (
        <div className="h-[80%] bg-slate-400">
          <section className="h-[65%] bg-slate-300 grid grid-cols-5 gap-2 content-center">
            <article className="col-span-3 justify-center px-6 content-center">
              <h3 className="text-xl indent-4 whitespace-pre-wrap text-justify">
                {calculusTask.question}
              </h3>
            </article>
            <figure className="col-span-2 xl:p-4 grid justify-center content-center">
              <img
                src={calculusTask.imageUrl}
                alt="Task Diagram"
                className=" h-[100%] w-[100%]"
              />
            </figure>
          </section>

          <section className="h-[35%] grid grid-rows-4 grid-cols-4">
            <div className="col-span-3 row-span-4 flex flex-col content-center">
              {calculusTask.answers &&
                calculusTask.answers.map((answer, index) => {
                  return (
                    <CalculusAnswer
                      key={index}
                      answer={answer}
                      setAnswers={(updatedAnswer) => {
                        const updatedAnswers = [...currentAnswers];
                        updatedAnswers[index] = updatedAnswer;
                        setCurrentAnswers(updatedAnswers);
                      }}
                      isFirstRound={isFirstRound}
                    />
                  );
                })}
            </div>

            <div className="grid col-span-1 row-span-4 bg-slate-400 justify-end content-center">
              <button
                id="send-btn"
                className="bg-orange-400 h-min min-w-min self-center px-4 py-4 my-6 mr-28 rounded-md text-white font-bold"
                onClick={handleSendTaskClick}
              >
                Enviar Tarea
              </button>
            </div>
          </section>
        </div>
      )}

      <nav className="h-[5%] bg-blue-400 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

interface CalculusAnswerTaskProps {
  answer: CalculusTaskAnswer;
  setAnswers: (answer: CalculusTaskAnswer) => void;
  isFirstRound: boolean;
}

function CalculusAnswer({ answer, setAnswers }: CalculusAnswerTaskProps) {
  const [inputValue, setInputValue] = useState<string | null>(
    answer.value ? answer.value?.toString() : null
  );

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    // Give 5 decimal precision to the input value
    const newValue = isNaN(parseFloat(e.target.value))
      ? "0"
      : parseFloat(e.target.value).toFixed(5);

    // Update the local state for input value
    setInputValue(newValue);
    setAnswers({
      ...answer,
      value: newValue ? parseFloat(newValue) : 0,
    });
  };

  return (
    <div className="h-full flex justify-center gap-4 items-center">
      <h3 className="text-xl text-justify">{answer.variableName}:</h3>

      <input
        aria-label="answer-input"
        type="number"
        value={inputValue ? parseFloat(inputValue) : ""}
        onChange={handleInputChange}
        className="h-[50%] text-center px-2 rounded-md"
      />
      <figcaption className="bg-white h-[50%] w-[30%] border-2 border-gray-900 px-2 text-md grid content-center text-center justify-items-center cursor-default">
        {answer.correctUnit}
      </figcaption>
    </div>
  );
}

export default CalculusTask;
