import configData from "../config.json";

interface CalculusTaskModel {
    imageUrl: string;
    question: string;
    answers: CalculusTaskAnswer[];
  }
  
  interface CalculusTaskAnswer {
    variableName: string;
    value: number | null;
    correctValue: number;
    unit: string | null;
    correctUnit: string;
  }

  
export async function GetTask(id: number ): Promise<CalculusTaskModel> {
    return fetch(`${configData.SERVER_URL}/calculusTasks/${id}/`, 
    {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
        },
    }
    )
    .then( (response) => response.json())
    .then((data) => {

        console.log(data);

        const task: CalculusTaskModel = {
            imageUrl: data.questions[0].diagram? data.questions[0].diagram : "",
            question: data.questions[0].problem_statement? data.questions[0].problem_statement : "",
            answers: data.questions[0].answers.map((answer: any) => {
                return {
                    variableName: answer.variable_name? answer.variable_name : "",
                    value: null,
                    correctValue: answer.answer_value? answer.answer_value : 0,
                    unit: answer.unit? answer.unit : "",
                    correctUnit: answer.measurement_unit? answer.measurement_unit : ""
                }
            }),
        }
        return task;

    })
    .catch((error)=> {
        console.log(error);
        throw error; // Rethrow the error to be caught by the caller
    });
}

export async function SendAttempt( userId: number, taskId: number, answers: CalculusTaskAnswer[] ): Promise<any> {
    return fetch(`${configData.SERVER_URL}/calculusTaskAttempt/`,
        {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                student: userId,
                task: taskId,
                variable_values: answers.map((answer) => {
                    return {
                        variable_name: answer.variableName,
                        value: answer.value,
                    }
                })
            })
        }
    )
}

export async function SendSkippedAttempt( userId: number, taskId: number ): Promise<any> {
    return fetch(`${configData.SERVER_URL}/calculusTaskAttempt/`,
        {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                student: userId,
                task: taskId,
                skipped: true,
            })
        }
    )
}

