import { useState } from "react";
import { SendAttempt } from "../../services/CalculusTaskService"
// This is a component that goes between rounds in each task
// It must recieve props from the CalculusTask component: which answers
// which were incorrect
// Additionally, this component must only be rendered when the user didn't get
// all the answers correct on the firstRound of the task

interface CalculusTaskAnswer {
  variableName: string;
  value: number | null;
  correctValue: number;
  unit: string | null;
  correctUnit: string;
}

interface CalculusIntersticeProps {
  incorrectAnswers: CalculusTaskAnswer[];
  correctAnswers: CalculusTaskAnswer[];
  setIsFirstRound: React.Dispatch<React.SetStateAction<boolean>>;
  isFirstRound: boolean;
  setInterstice: React.Dispatch<React.SetStateAction<boolean>>;
  setCurrentView: (view: view) => void;
  studentId: number;
  taskId: number;
}

interface view {
  view: string;
  taskId?: number;
}

function CalculusInterstice({
  incorrectAnswers,
  correctAnswers,
  setIsFirstRound,
  isFirstRound,
  setInterstice,
  setCurrentView,
  studentId,
  taskId,
}: CalculusIntersticeProps) {
  const [allAnswers, setAllAnswers] = useState<CalculusTaskAnswer[]>(
    incorrectAnswers.concat(correctAnswers)
  );
  const [isLoading, setIsLoading] = useState(false);

  const isIncorrect = (answer: CalculusTaskAnswer) => {
    if (answer.value === null) {
      return true;
    }
    const incorrectPercentage: number = Math.floor(
      (Math.abs(answer.correctValue - answer.value) / answer.correctValue) * 100
    );
    if (incorrectPercentage > 5) {
      return true;
    }
    return false;
  };

  const generateErrorMessage = (answer: CalculusTaskAnswer) => {
    // Generate the error message when isFirstRound changes

    const pickedAnswer = {
      value: answer.value ? answer.value : 0,
    };

    let errorMessage: string = "";
    let incorrectPercentage: number = Math.floor(
      (Math.abs(answer.correctValue - pickedAnswer.value) /
        answer.correctValue) *
        100
    );

    // The percentage of error is more than 5%, it's wrong
    if (incorrectPercentage >= 5) {
      let percentageHint: string = "5";
      if (incorrectPercentage >= 50) {
        percentageHint = "50";
      } else if (incorrectPercentage >= 30) {
        percentageHint = "30";
      } else if (incorrectPercentage >= 20) {
        percentageHint = "20";
      }

      console.log(`Values are different on a ${incorrectPercentage}%`);
      console.log("pickedAnswer.value: ", pickedAnswer.value);
      console.log("answer.correctValue: ", answer.correctValue);
      errorMessage += `El valor de ${answer.variableName} es incorrecto en al menos un ${percentageHint}%.`;
    }

    return errorMessage;
  };

  const handleTryAgain = () => {
    setInterstice(false);
    setIsFirstRound(false);
  };

  const finishTask = () => {
    console.log("Task finished");
    // Animate the finish task button to show that we're now loading

    setIsLoading(true);

    // Send the task Attempt to the server
    SendAttempt(studentId, taskId, allAnswers).then((data) => {
      console.log(data);
      setIsLoading(false);
      setCurrentView({view: "home", taskId: undefined});
    }).catch((error) => {
      console.log(`Error sending task attempt: ${error}`);
      setIsLoading(false);
    })

  };

  return (
    <main className="grid grid-cols-5 bg-slate-400 justify-center items-center h-[80%] w-full">
      <div className="h-full w-full bg-blue-200 flex col-span-4 gap-4 justify-evenly items-center">
        {/* Map incorrect Answers  */}
        {allAnswers.map((answer, index) => {
          return (
            <div
              key={index}
              className={`grid grid-rows-5 grid-cols-1 w-[23%] max-w-[23%] h-[70%] bg-white rounded-lg justify-center content-center ${isIncorrect(answer) ? `animate-fade-in-down` : `animate-fade-in-up`}`}
            >
              <section
                className={`flex flex-col row-span-3 w-full bg-gradient-to-b ${
                  isIncorrect(answer)
                    ? "from-red-400 to-red-500"
                    : "from-green-400 to-green-500"
                }  rounded-t-md px-8 py-2`}
              >
                <h3 className="text-4xl mt-1 mb-8 col-span-1 w-full text-justify border-b-[1px] border-white">
                  {answer.variableName}
                </h3>
                <div className="col-span-1 self-start 2xl:text-7xl lg:text-5xl grid content-center font-thin text-center justify-items-center cursor-default">
                  {answer.value}
                </div>
                <figcaption className="col-span-2 max-w-fit text-lg grid content-center text-center justify-items-center cursor-default">
                  {answer.correctUnit}
                </figcaption>
                {/* <figure className="w-[25%] justify-self-end self-end">
                    <svg
                      fill="#000000"
                      viewBox="0 0 24 24"
                      xmlns="http://www.w3.org/2000/svg"
                    >
                      <g id="SVGRepo_bgCarrier" stroke-width="0"></g>
                      <g
                        id="SVGRepo_tracerCarrier"
                        stroke-linecap="round"
                        stroke-linejoin="round"
                      ></g>
                      <g id="SVGRepo_iconCarrier">
                        <path d="M9.172 16.242 12 13.414l2.828 2.828 1.414-1.414L13.414 12l2.828-2.828-1.414-1.414L12 10.586 9.172 7.758 7.758 9.172 10.586 12l-2.828 2.828z"></path>
                        <path d="M12 22c5.514 0 10-4.486 10-10S17.514 2 12 2 2 6.486 2 12s4.486 10 10 10zm0-18c4.411 0 8 3.589 8 8s-3.589 8-8 8-8-3.589-8-8 3.589-8 8-8z"></path>
                      </g>
                    </svg>
                </figure> */}
              </section>

              <section className="row-span-2">
                {isIncorrect(answer) ? (
                  <figcaption className=" h-full px-2 text-lg whitespac-pre-line grid content-center text-center justify-items-center cursor-default">
                    <p>{generateErrorMessage(answer)}</p>
                  </figcaption>
                ) : (
                  <div className="grid h-full items-end justify-items-end p-5">
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      x="0px"
                      y="0px"
                      width="100"
                      height="100"
                      viewBox="0 0 48 48"
                    >
                      <path
                        fill="#c8e6c9"
                        d="M44,24c0,11.045-8.955,20-20,20S4,35.045,4,24S12.955,4,24,4S44,12.955,44,24z"
                      ></path>
                      <path
                        fill="#4caf50"
                        d="M34.586,14.586l-13.57,13.586l-5.602-5.586l-2.828,2.828l8.434,8.414l16.395-16.414L34.586,14.586z"
                      ></path>
                    </svg>
                  </div>
                )}
              </section>
            </div>
          );
        })}
      </div>
      <div className="h-full w-full flex flex-col gap-6 col-span-1 justify-center items-center bg-blue-300 border-black border-l-[1px] ">
        {isFirstRound && incorrectAnswers.length !== 0 ? (
          <>
            <h2 className="text-4xl text-center">Lo intentamos denuevo? 🤔</h2>
            <button
              className="bg-white hover:bg-slate-100 p-4 h-[10%] w-[66%] border-black border-2"
              onClick={handleTryAgain}
            >
              Si! 💪
            </button>
            <button 
            className={`${isLoading ? `animate-bounce` : ``} bg-white hover:bg-slate-100 p-4 min-h-[10%] max-h-fit w-[66%] border-black border-2`}
            onClick={finishTask}
            >
              Enviar de todas maneras ✉️
            </button>
          </>
        ) : (
          <>
            <h2 className="text-4xl text-center mb-4">Eso es todo! 🤓</h2>
            <button className={`${isLoading ? `animate-bounce` : ``} bg-white hover:bg-slate-100 p-4 min-h-[10%] max-h-fit w-[66%] border-black border-2`}
            onClick={finishTask}
            >
              Volver al menu 🏠
            </button>
          </>
        )}
      </div>
    </main>
  );
}

export default CalculusInterstice;
