import Concept from "./Concept";
import { useEffect, useState } from "react";
import { GetStudentData } from "../services/StudentService";
import {GetNextTask} from "../services/StudentService";

interface ConceptData {
  id: number;
  name: string;
  score: number;
}

interface currentTask {
  type: string;
  id: number;
}

interface view {
  view: string;
  taskId?: number;
}

interface HomeProps {
  setCurrentView: (view: view) => void;
  setCurrentTask: (currenTask: currentTask) => void;
  currentTask: currentTask;
  currentUserId: number;
}

function Home({
  setCurrentView,
  setCurrentTask,
  currentUserId,
}: HomeProps) {
  const [isLoading, setIsLoading] = useState(true);
  const [userName, setUserName] = useState("");

  const [concepts, setConcepts] = useState<ConceptData[]>([
    {
      id: 1,
      name: "Tipos de ondas",
      score: 0,
    },
    {
      id: 2,
      name: "Características de las ondas",
      score: 0,
    },
    {
      id: 3,
      name: "Ondas estacionarias y viajeras",
      score: 0,
    },
    {
      id: 4,
      name: "Ondas sonoras",
      score: 0,
    },
    {
      id: 5,
      name: "Movimiento de una cuerda",
      score: 0,
    },
  ]);


  
  useEffect(() => {
    // Get the student data from the server
    GetStudentData(currentUserId).then((data) => {
      console.log(data);
      setUserName(data.student_name);

      // Create a new array of concepts with updated scores
      const updatedConcepts = concepts.map((concept) => {
        const score = data.scores[concept.id.toString()]; // Get the score from the data
        if (score !== undefined) {
          // Check if a score exists in the data for this concept
          return { ...concept, score: score };
        }
        return concept; // If no score is found, use the original concept
      });

      // Update the concepts state with the updated array
      setConcepts(updatedConcepts);
    });
    // Get the task order from the server
    // Set the task order in the state
    setIsLoading(false);
  }, [currentUserId]);

  const handleClick = () => {
    // Get the task from the server
    GetNextTask(currentUserId).then((data) => {
      // console.log(data);
      // Set the task in the state
      setCurrentTask(data);
      // Check what kind of task it is (alternatives, calculus)
      // Set the current view to the corresponding task
      if(data.task_type === "Alternative"){
        setCurrentView({view : "alternativesTask", taskId: data.task_id});
      } else if(data.task_type === "Calculus"){
        setCurrentView({view: "calculusTask", taskId: data.task_id});
      }
    }).catch((error) => {
      console.log(error);
      return;
    });

  };

  const handleLogout = () => {
    setCurrentView({ view: "login", taskId: undefined});
  };

  return (
    <div className="h-screen">
      <nav className="h-[15%] bg-orange-400 flex flex-row-reverse gap-10 px-28 content-center">
        <div 
        className="text-center self-center text-lg cursor-pointer"
        onClick={handleLogout}
        >
          <svg
            xmlns="http://www.w3.org/2000/svg"
            className="hover:bg-gray-200 rounded-full "
            width="24"
            height="24"
            viewBox="0 0 24 24"
            strokeWidth="2"
            stroke="currentColor"
            fill="none"
            strokeLinecap="round"
            strokeLinejoin="round"
          >
            {" "}
            <path stroke="none" d="M0 0h24v24H0z" fill="none" />{" "}
            <path d="M13 12v.01" /> <path d="M3 21h18" />{" "}
            <path d="M5 21v-16a2 2 0 0 1 2 -2h7.5m2.5 10.5v7.5" />{" "}
            <path d="M14 7h7m-3 -3l3 3l-3 3" />{" "}
          </svg>
        </div>
        <div className="px-2 py-2 text-center self-center text-2xl">
          Hola {userName} 👋
        </div>
      </nav>
      <article className="h-[80%] grid grid-cols-2">
        <div className="flex flex-col gap-8 justify-center content-center text-center bg-blue-400 px-6">
          {concepts.map((concept) => (
            <Concept concept={concept} key={concept.id} />
          ))}
        </div>
        <div className="bg-red-400 flex justify-center content-center">
          <button
            className="bg-slate-300 hover:bg-slate-100 self-center py-20 px-8 text-4xl border-2 border-black"
            onClick={handleClick}
          >
            <p>Hagamos una tarea!</p>
            <p>🤓 🤓</p>
          </button>
        </div>
      </article>
      <nav className="h-[5%] bg-orange-400 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

export default Home;
