import configData from "../config.json";

export async function GetTeacherId(username: string): Promise<number> {
  return fetch(`${configData.SERVER_URL}/teachers/?format=json`, {
    method: "GET",
    headers: {
      "Content-Type": "application/json",
    },
  })
    .then((response) => response.json())
    .then((data) => {
      console.log(data);
      const teacher = data.filter((teacher: any) => teacher.name === username);
      const id = teacher[0].id;
      return id;
    })
    .catch((error) => {
      console.log(error);
      throw error;
    });
}

export async function CreateTeacher(username: string): Promise<any> {
    return fetch(`${configData.SERVER_URL}/teachers/`, {
        method: "POST",
        headers: {
        "Content-Type": "application/json",
        },
        body: JSON.stringify({ name: username }),
    })
        .then((response) => response.json())
        .then((data) => {
        console.log(data);
        return data;
        })
        .catch((error) => {
        console.log(error);
        throw error;
        });
    }