import {useEffect, useState} from "react";

interface ConceptProps {
  concept: ConceptData;
}

interface ConceptData {
  name: string;
  score: number;
}

function Concept({ concept }: ConceptProps) {
    // Local state to handle the animated width
    const [barWidth, setBarWidth] = useState(0);

    useEffect(() => {
        // Update the width after the component is mounted to initiate the animation
        setBarWidth(concept.score ? concept.score * 10 : 0);

    }, [concept.score]);

  return (
    <div className="w-full grid grid-cols-3 gap-6 justify-center content-center">
      <h3 className="col-span-1 my-auto">{concept.name}</h3>
      <div className="group col-span-2">

        {/* Hover tooltip with score */}
        <div className="relative hidden group-hover:block">
          <div className="absolute -bottom-3 left-1/2 bg-gray-300 group-hover:text-black text-xs px-2 py-1 rounded cursor-default">
            {concept.score ? concept.score * 10 : 0}%
          </div>
        </div>


        {/* Bar */}
        <div className="bg-gray-300 h-10 my-auto align-self-center">
          <div
            style={{ width: `${barWidth}%` }}
            className="progress-bar bg-green-300 h-full border-2 border-black"
          ></div>


        </div>
      </div>
    </div>
  );
}

export default Concept;
