import { DateTime } from "luxon";
import "chart.js/auto";
import { Bar } from "react-chartjs-2";
import { useState, useEffect } from "react";
import { GetAllStudents } from "../../services/StudentService";
import { GetStudentDashboard } from "../../services/DashboardService";
import {
  GetAttempt as GetCalculusAttempt,
  GetTask as GetCalculusTask,
} from "../../services/CalculusTaskService";
import {
  GetAttempt as GetAlternativeAttempt,
  GetTask as GetAlternativeTask,
} from "../../services/AlternativeTaskService";
import Concept from "../Concept";

interface view {
  view: string;
  taskId?: number;
}

interface StudentDashboardProps {
  setCurrentView: (view: view) => void;
}

// Define interfaces for the data returned by GetStudentDashboar
interface StudentDashboardData {
  concepts_data: Record<string, number>;
  time_spent: number;
  last_attempt: {
    attempt_id: number;
    task_id: number;
    date: string;
    type: string;
  };
  time_spent_per_concept: Record<string, number>;
  average_time_per_concept_all_students: Record<string, number>;
}

interface ConceptData {
  id: number;
  name: string;
}

function StudentDashboard({ setCurrentView }: StudentDashboardProps) {
  const [isLoading, setIsLoading] = useState(true);
  const [selectedStudent, setSelectedStudent] = useState<number>(0);
  const [fetchedStudents, setFetchedStudents] = useState<
    { id: number; name: string }[]
  >([]);
  const [fetchedData, setFetchedData] = useState<StudentDashboardData | null>(
    null
  );
  const [concepts, setConcepts] = useState<ConceptData[]>([
    {
      id: 1,
      name: "Tipos de ondas",
    },
    {
      id: 2,
      name: "Características de las ondas",
    },
    {
      id: 3,
      name: "Ondas estacionarias y viajeras",
    },
    {
      id: 4,
      name: "Ondas sonoras",
    },
    {
      id: 5,
      name: "Movimiento de una cuerda",
    },
  ]);

  useEffect(() => {
    // Fetch student data when the component mounts
    GetAllStudents().then((data) => {
      setFetchedStudents(data);
      setIsLoading(false);
    });
  }, []);

  useEffect(() => {
    // Fetch student dashboard data when a student is selected
    if (selectedStudent !== 0) {
      setIsLoading(true);
      GetStudentDashboard(selectedStudent).then((data) => {
        setFetchedData(data);
        setIsLoading(false);
      });
    }
  }, [selectedStudent]);

  const handleStudentChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
    // Update the selected student when the dropdown value changes
    const studentId = parseInt(event.target.value);
    setSelectedStudent(studentId);
  };

  const chartData = {
    labels: concepts.map((concept) => concept.name), // Use concept names as labels
    datasets: [
      {
        label: "Tiempo Invertido (Estudiante Actual)",
        backgroundColor: "rgba(75, 192, 192, 0.6)",
        borderColor: "rgba(75, 192, 192, 1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(75, 192, 192, 0.8)",
        hoverBorderColor: "rgba(75, 192, 192, 1)",
        data: concepts.map(
          (concept) =>
            fetchedData?.time_spent_per_concept[concept.id.toString()] || 0
        ), // Get time spent data for each concept
      },
      {
        label: "Tiempo Promedio Invertido (Todos los Estudiantes)",
        backgroundColor: "rgba(192, 75, 75, 0.6)",
        borderColor: "rgba(192, 75, 75, 1)",
        borderWidth: 1,
        hoverBackgroundColor: "rgba(192, 75, 75, 0.8)",
        hoverBorderColor: "rgba(192, 75, 75, 1)",
        data: concepts.map(
          (concept) =>
            fetchedData?.average_time_per_concept_all_students[
              concept.id.toString()
            ] || 0
        ), // Get average time spent data for each concept
      },
    ],
  };

  const chartOptions = {
    scales: {
      y: {
        beginAtZero: true,
      },
    },
  };

  function formatDate(dateString: string): string {
    const dateObj = DateTime.fromISO(dateString, { zone: "utc" }); // Ensure it's parsed as UTC
    const formattedDate = dateObj
      .setZone("America/Argentina/Buenos_Aires")
      .toFormat("dd/MM/yyyy HH:mm");
    return formattedDate;
  }

  const handleGoBack = () => {
    setCurrentView({ view: "teacherHome" });
  };
  return (
    <div className="h-screen">
      <header className="w-full h-[10%] bg-blue-200 grid grid-cols-5 gap-10 justify-center px-28 content-center">
        <div className="px-2 py-2 col-start-1 col-end-4 text-right  text-5xl">
          Dashboard Estudiante
        </div>
        <button
          type="button"
          className="bg-slate-400 h-min col-start-5 col-end-5 px-4 py-2 rounded-md text-white font-bold text-4xl"
          onClick={handleGoBack}
        >
          🏠
        </button>
      </header>
      <section className="h-[85%] justify-items-center items-center">
        {isLoading ? (
          <div className="flex justify-center items-center h-[80%]">
            <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
          </div>
        ) : (
          <>
            <div className="flex pt-2 w-[95%] h-[7%]">
              <label htmlFor="studentSelect" className="text-xl text-center">
                Selecciona un estudiante:
              </label>
              <select
                id="studentSelect"
                className="block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
                onChange={handleStudentChange}
                value={selectedStudent}
              >
                <option value={0}>Select a student</option>
                {fetchedStudents.map((student) => (
                  <option key={student.id} value={student.id}>
                    {student.name}
                  </option>
                ))}
              </select>
            </div>

            <section className="h-[93%] grid grid-rows-2 grid-cols-2 items-center justify-items-center">
              <article className="h-[80%] w-[75%] bg-gray-200 flex flex-col gap-2 items-center justify-center">
                <h2 className="text-md font-bold h-[6%]">Avance por Concepto</h2>
                <div className="flex flex-col gap-1 h-[94%] justify-center content-center text-center text-xs px-6">
                  {fetchedData &&
                    Object.entries(fetchedData.concepts_data).map(
                      ([conceptId, score]) => {
                        const conceptName = concepts.find(
                          (concept) => concept.id === parseInt(conceptId)
                        )?.name;

                        if (!conceptName) return null; // Skip if no matching concept found

                        return (
                          <div className="h-[15%]">

                          <Concept
                            key={conceptId}
                            concept={{ name: conceptName, score: score }}
                            />
                            </div>
                        );
                      }
                    )}
                </div>
              </article>
              <article className="h-[75%] w-[75%] bg-gray-100 flex flex-col items-center justify-center">
                <h2 className="text-md font-bold">Tiempo Usado en la App</h2>
                <div className="h-[80%] w-[35%] rounded-full flex flex-col gap-1 items-center justify-center border-4 border-cyan-500 ">
                  <h4 className="text-6xl font-bold">
                    {fetchedData && fetchedData.time_spent}
                  </h4>
                  <h5 className="">segundos</h5>
                </div>
              </article>
              <article className="h-[75%] w-[75%] bg-gray-100 flex flex-col items-center justify-center">
                <h2 className="text-md font-bold">
                  Tiempo Invertido por Concepto (segundos)
                </h2>
                <div className="w-full">
                  <Bar data={chartData} options={chartOptions} />
                </div>
              </article>
              <article className="h-[100%] w-[90%] bg-gray-100 flex flex-col items-center justify-center">
                <h2 className="text-md font-bold">Ultima Tarea Enviada</h2>
                {fetchedData && (
                  <>
                    <span>
                      {fetchedData && formatDate(fetchedData.last_attempt.date)}
                    </span>
                    {fetchedData.last_attempt.type === "Calculus" ? (
                      <CalculusMiniAttempt
                        attempt_id={fetchedData.last_attempt.attempt_id}
                        task_id={fetchedData.last_attempt.task_id}
                      /> 
                    ) : (
                      <MiniAlternativeTask 
                      attempt_id={fetchedData.last_attempt.attempt_id}
                      task_id={fetchedData.last_attempt.task_id} 
                      />
                    )
                    }
                  </>
                )}
              </article>
            </section>
          </>
        )}
      </section>
      <nav className="h-[5%] bg-blue-200 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

interface CalculusTaskModel {
  imageUrl: string;
  question: string;
  answers: CalculusTaskAnswer[];
}

interface CalculusTaskAnswer {
  variableName: string;
  value: number | null;
  correctValue: number;
  unit: string | null;
  correctUnit: string;
}

interface VariableValue {
  id: number;
  variable_name: string;
  user_input_value: number;
}

function CalculusMiniAttempt({
  task_id,
  attempt_id,
}: {
  task_id: number;
  attempt_id: number;
}) {
  const [isLoading, setIsLoading] = useState(true);
  const [fetchedTaskData, setFetchedTaskData] = useState<any | null>(null);
  const [calculusTask, setCalculusTask] = useState<CalculusTaskModel>({
    imageUrl: "https://picsum.photos/500/500",
    question: "What is the area under the curve?",
    answers: [
      {
        variableName: "a",
        correctValue: 10,
        value: null,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "b",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "c",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
      {
        variableName: "d",
        correctValue: 10,
        value: 0,
        correctUnit: "m",
        unit: null,
      },
    ],
  });

  useEffect(() => {
    setIsLoading(true);
    GetCalculusAttempt(attempt_id).then((data) => {
      setFetchedTaskData(data);
    }).catch((error) => {
      console.log(error);
    }
    );
    GetCalculusTask(task_id).then((data) => {
      setCalculusTask(data);
    }).catch((error) => {
      console.log(error);
    }
    );
    setIsLoading(false);
  }, [attempt_id]);

  const isIncorrect = (answer: CalculusTaskAnswer) => {
    if (answer.value === null) {
      return true;
    }
    const incorrectPercentage: number = Math.floor(
      (Math.abs(answer.correctValue - answer.value) / answer.correctValue) * 100
    );
    if (incorrectPercentage > 5) {
      return true;
    }
    return false;
  };


  return (
    <div className="grid grid-rows-3 grid-cols-3">
      <section className="grid grid-cols-2 row-span-2 col-span-3">
        {isLoading ? (
          <div className="flex justify-center items-center h-[80%]">
            <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
          </div>
        ) : (
          <>
            <div className="flex justify-center items-center px-4">
              <p className="text-xs">{calculusTask.question}</p>
            </div>
            <div className="flex justify-center items-center">
              <img
                src={calculusTask.imageUrl}
                alt="Task Diagram"
                className="h-[80%] w-[80%]"
              />
            </div>
          </>
        )}
      </section>
      <section className="flex row-span-1 col-span-3">
        {isLoading ? (
          <div className="flex justify-center items-center h-[80%]">
            <div className="animate-spin rounded-full h-32 w-32 border-t-2 border-b-2 border-gray-900"></div>
          </div>
        ) : (
          <>
            <div className="flex justify-center items-center px-4">
              <p className="text-xs">Respuestas</p>
            </div>
            <div className="flex justify-center items-center">
              <div className="flex gap-1 justify-center content-center text-center ">
                {fetchedTaskData?.variable_values.map(
                  (variable: VariableValue) => (
                    <div
                      key={variable.variable_name}
                      className={`flex flex-col gap-2 p-2 border-gray-300 border rounded-md shadow-md
                      ${
                        isIncorrect({
                          variableName: variable.variable_name,
                          value: variable.user_input_value,
                          correctValue:
                            calculusTask.answers.find(
                              (answer) =>
                                answer.variableName === variable.variable_name
                            )?.correctValue || 0,
                          unit: null,
                          correctUnit: "",
                        })
                          ? `bg-red-500`
                          : `bg-green-500`
                      }
                      `}
                    >
                      <p
                        key={variable.variable_name}
                        className="text-xs text-right border-b-black "
                      >
                        {variable.variable_name}
                      </p>
                      <p key={variable.variable_name} className="text-xs">
                        {variable.user_input_value}
                      </p>
                      <p key={variable.variable_name} className="text-xs">
                        {
                          calculusTask.answers.find(
                            (answer) =>
                              answer.variableName === variable.variable_name
                          )?.correctUnit
                        }
                      </p>
                    </div>
                  )
                )}
              </div>
            </div>
          </>
        )}
      </section>
    </div>
  );
}
interface SelectedAnswer {
  id: number;
  answer_text: string;
  incorrect_hint: string;
  is_correct: boolean;
  question: number;
}

interface AlternativetaskAttempt {
  id: number;
  date: string;
  elapsed_time: number;
  skipped: boolean;
  student: number;
  task: number;
  selected_answers: SelectedAnswer[];
}
interface AlternativesQuestionModel {
  question: AlternativesQuestion;
  answers: AlternativesAnswer[];
  savedPickedAnswer: AlternativesAnswer | null;
}

interface AlternativesQuestion {
  text: string;
}

interface AlternativesAnswer {
  text: string;
  isCorrect: boolean;
  incorrectHint: string;
}

interface MiniAlternativeTaskProps {
  task_id: number;
  attempt_id: number;
}

function MiniAlternativeTask( {task_id, attempt_id}: MiniAlternativeTaskProps) {
  const [isLoading, setIsLoading] = useState(true);
  const [fetchedQuestions, setFetchedQuestions] = useState<AlternativesQuestionModel[]>([]);
  const [fetchedTaskData, setFetchedTaskData] = useState<AlternativetaskAttempt | undefined>(undefined);

  useEffect(() => {
    setIsLoading(true);
    GetAlternativeTask(task_id).then((data) => {
      setFetchedQuestions(data.questions);
    }).catch((error) => {
      console.log(error);
    });
    GetAlternativeAttempt(attempt_id).then((data) => {
      setFetchedTaskData(data);
    }).catch((error) => {
      console.log(error);
    });
    setIsLoading(false);
    
  }, [attempt_id])

  return (
    <div className="flex gap-1 justify-evenly items-center ">
      {fetchedQuestions.map((question) => (
        <MiniAlternativeQuestion key={question.question.text} question={question} fetchedTaskData={fetchedTaskData}/>
        
      ))}
    </div>
  )

}

interface MiniAlternativeQuestionProps {
  question: AlternativesQuestionModel;
  fetchedTaskData?: AlternativetaskAttempt;
}
function MiniAlternativeQuestion( {question, fetchedTaskData}: MiniAlternativeQuestionProps) {


  const isSelected = (answer: AlternativesAnswer) => {
    if (fetchedTaskData === undefined) return false;
    const selectedAnswer = fetchedTaskData.selected_answers.find((selectedAnswer) => selectedAnswer.answer_text === answer.text);
    if (selectedAnswer === undefined) return false;
    return selectedAnswer.answer_text === answer.text;
  }

  return (
    <div className="min-w-[15%] max-w-[20%] p-2 bg-gray-200 border-slate-500 rounded-md">
      <p className="text-xs text-center">{question.question.text}</p>
      <div className="flex flex-col gap-1">
        {question.answers.map((answer) => (
          <MiniAlternativeAnswer key={answer.text} answer={answer} selected={isSelected(answer)} />
        ))}
      </div>

    </div>
  )
}

interface MiniAlternativeAnswerProps {
  answer: AlternativesAnswer;
  selected: boolean;
}

function MiniAlternativeAnswer( {answer, selected}: MiniAlternativeAnswerProps) {

  const generateStyle = (answer : AlternativesAnswer ) => {
    let styleString  : string = ``;
    if(selected ){
      styleString += `border-2 border-black bg-gradient-to-b`;
    if(answer.isCorrect){
      styleString += ` from-green-400 to-green-500`;
    }else{
      styleString += ` from-red-400 to-red-500`;
    }
}
else {
  styleString += `bg-blue-600`
}
    return styleString;

  }

  return (
    <div className={`${generateStyle(answer)} text-white rounded-md text-center text-xs py-2 px-1`}>
      <p>{answer.text}</p>
    </div>
  )
}

export default StudentDashboard;
