import { AlternativesQuestionModel, AlternativesAnswer } from "./Interfaces";

interface TaskFinishedProps {
  sentTask: AlternativesQuestionModel[];
  setCurrentView: (view: view) => void;
}

interface view {
  view: string;
  taskId?: number;
}

function TaskFinished({ sentTask, setCurrentView }: TaskFinishedProps) {
  const handleHomeClick = () => {
    // Set current view to home
    setCurrentView({view: "home", taskId: undefined});
  };

  return (
    <div className="h-screen cursor-default">
      <header className="h-[10%] flex items-center justify-center bg-green-200">
        <h1 className="col-span-3 text-center text-4xl font-bold">
          Resultados
        </h1>
      </header>
      <section className="h-[80%] bg-gray-50 flex flex-col items-center justify-items-center">
          {/* Cards */}
          <section className="h-[90%] flex gap-[1.5%] text-center justify-evenly items-center text-2xl font-bold">
            { sentTask.map((question, index) => (
              <QuestionCard key={index} question={question} />
            
            ))}
          </section>

          {/* Home Button */}
          <section className="h-[15%] w-full flex items-center justify-center">
            <button
              className="text-center text-5xl font-bold border-2 bg-gray-100 hover:bg-gray-200 border-black rounded-md py-2 w-1/6 mx-auto"
              onClick={handleHomeClick}
            >
              🏠
            </button>
          </section>
      </section>
      <nav className="h-[10%] bg-green-200 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

function QuestionCard({ question }: { question: AlternativesQuestionModel }) {

  const generateStyle = (question : AlternativesQuestionModel, answer : AlternativesAnswer ) => {
    let styleString  : string = ``;
    if(question.savedPickedAnswer?.text === answer.text ){
      styleString += `border-2 border-black bg-gradient-to-b`;
    if(answer.isCorrect){
      styleString += ` from-green-400 to-green-500`;
    }else{
      styleString += ` from-red-400 to-red-500`;
    }
}
    return styleString;

  }

  return (
    
    <article className="h-[80%] w-[18%] bg-gray-100 rounded-lg shadow-md animate-fade-in-down">
      <header className="text-center max-h-[30%] min-h-[15%] py-2 px-3 text-sm font-bold bg-gradient-to-r from-blue-500 to-blue-400 rounded-t-lg">
        {question.question.text}
      </header>
        <main className="flex flex-col justify-evenly items-center h-[70%]" >
          {question.answers.map((answer, index) => (
            <figure key={index} className='group w-full flex justify-evenly'>

              {/* Hover tooltip with hint */}
                <div className={`relative hidden ${ (question.savedPickedAnswer?.text === answer.text) && !answer.isCorrect ? `group-hover:block` : ``}`}>
                  <div className="absolute -bottom-1/2 left-full bg-gray-300 group-hover:text-black text-xs px-2 py-1 rounded cursor-default">
                    {answer.incorrectHint}
                  </div>

              {/* Card text */}
              </div>
            <div  className={`text-sm w-[90%] bg-white py-4 px-2 rounded-lg ${generateStyle(question, answer)}`}>
              {answer.text}
            </div>
            </figure>
          ))}
        </main>
    </article>
  );
}

export default TaskFinished;
