import { useState } from "react";
import { GetId, CreateStudent } from "../services/StudentService";
import { GetTeacherId, CreateTeacher } from "../services/TeacherService";

interface LoginProps {
  setCurrentView: (view: view) => void;
  setCurrentUserId: (userId: number) => void;
}

interface view {
  view: string;
  taskId?: number;
}

function Login({ setCurrentView, setCurrentUserId }: LoginProps) {
  const [username, setUsername] = useState("");
  const [role, setRole] = useState("Estudiante");

  const handleUsernameChange = (event: any) => {
    setUsername(event.target.value);
  };

  const handleRoleChange = (event: any) => {
    setRole(event.target.value);
  };

  const handleLogin = () => {
    // You can perform login logic here, for example, sending the username and role to a server.
    console.log("Logging in with username:", username, "and role:", role);

    // If no username is provided, then animate login button
    if (username === "") {
        console.log("No username provided");

      const loginButton = document.getElementById("login-btn");
      loginButton?.classList.add("animate-wiggle");
      loginButton?.classList.remove("bg-green-400");
      loginButton?.classList.add("bg-red-500");
      setTimeout(() => {
          loginButton?.classList.remove("animate-wiggle");
          loginButton?.classList.remove("bg-red-500");
          loginButton?.classList.add("bg-green-400");
        // console.log("Wiggle removed")
      }, 500);
      return;
    }

    // If role is student, attempt to find the student in the database
    if ( role === "Estudiante") {
        GetId(username).then((id) => {
            console.log("Found Student with id:", id);
            setCurrentUserId(id);
            setCurrentView({view: "home", taskId: undefined});

        })
        .catch((error) => {
            console.log(error);
            const errorMsg = document.getElementById("error-msg");
            errorMsg?.classList.remove("hidden");
            // setTimeout(() => {
            //     errorMsg?.classList.add("hidden");
            // }, 2000);
            // setCurrentView("login");
        }
        );
    }
    else {
        // Set the id of the teacher and view to teacher-home
        GetTeacherId(username).then((id : number) => {
            console.log("Found Teacher with id:", id);
            setCurrentView({view: "teacherHome", taskId: undefined});
        }
        ).catch((error : any) => {
            console.log(error);
            const errorMsg = document.getElementById("error-msg");
            errorMsg?.classList.remove("hidden");
            // setTimeout(() => {
            //     errorMsg?.classList.add("hidden");
            // }, 2000);
            // setCurrentView("login");
        });
    }



    // Reset the input fields after login (if needed)
    setUsername("");
    setRole("Estudiante");
  };

  const handleSignUp = () => {
    // You can perform login logic here, for example, sending the username and role to a server.
    console.log("Signing up with username:", username, "and role:", role);

    // If no username is provided, then animate login button
    if (username === "") {
        console.log("No username provided");

      const loginButton = document.getElementById("login-btn");
      loginButton?.classList.add("animate-wiggle");
      loginButton?.classList.remove("bg-green-400");
      loginButton?.classList.add("bg-red-500");
      setTimeout(() => {
          loginButton?.classList.remove("animate-wiggle");
          loginButton?.classList.remove("bg-red-500");
          loginButton?.classList.add("bg-green-400");
        // console.log("Wiggle removed")
      }, 500);
      return;
    }

    // If role is student, attempt to find the student in the database
    if ( role === "Estudiante") {
      CreateStudent(username).then((data) => {
          const successMsg = document.getElementById("success-msg");  
          console.log("Created Student with id:", data.id);
            setCurrentUserId(data.id);
            setCurrentView({view: "home", taskId: undefined});
            successMsg?.classList.remove("hidden");
            

        })
        .catch((error) => {
            console.log(error);
            const errorMsg = document.getElementById("error-msg");
            errorMsg?.classList.remove("hidden");
            // setTimeout(() => {
            //     errorMsg?.classList.add("hidden");
            // }, 2000);
            // setCurrentView("login");
        }
        );
    }
    else {
        // Set the id of the teacher and view to teacher-home
        CreateTeacher(username).then((data : any) => {
          const successMsg = document.getElementById("success-msg");  

            console.log("Created Teacher with id:", data.id);
            setCurrentView({view: "teacherHome", taskId: undefined});
            successMsg?.classList.remove("hidden");
        }
        ).catch((error : any) => {
            console.log(error);
            const errorMsg = document.getElementById("error-msg");
            errorMsg?.classList.remove("hidden");
            // setTimeout(() => {
            //     errorMsg?.classList.add("hidden");
            // }, 2000);
            // setCurrentView("login");
        });
    }

  }

  return (
    <div className="h-screen flex flex-col items-center justify-center bg-gradient-to-br from-orange-400 to-orange-500">
      <article className="border-2 animate-fade-in-down  border-black w-[33%] h-[60%] rounded-t-xl shadow-lg bg-white">
        <h1 className="2xl:text-5xl text-3xl cursor-default rounded-t-xl bg-gradient-to-b from-blue-300 to-blue-400 text-center pt-2 h-[20%] border-b-2 border-black leading-relaxed">¡Bienvenido devuelta!</h1>
        <form className="w-full h-[70%] flex flex-col items-center justify-center ">
          <div className="grid w-full px-6 grid-cols-2 text-2xl">
            <label htmlFor="username">Username:</label>
            <input
              type="text"
              id="username"
              name="username"
              value={username}
              onChange={handleUsernameChange}
              placeholder="Ingresa tu usuario"
              className="border-2 text-center"
            />
          </div>
          <div className="grid w-full px-6 grid-cols-2 align-middle text-2xl" >
            <label htmlFor="role">Role:</label>
            <select
              id="role"
              name="role"
              value={role}
              onChange={handleRoleChange}
              className="border-2 text-center "
            >
              <option value="Estudiante">Estudiante</option>
              <option value="Profesor">Profesor</option>
            </select>
          </div>
        <p id="error-msg" className="underline animate-pulse text-red-500 pt-4 hidden">Usuario no encontrado</p>
        <p id="success-msg" className="underline animate-pulse text-green-500 pt-4 hidden">Usuario creado exitosamente</p>
        </form>
        <footer className="h-[10%] flex gap-4 flex-row-reverse px-5">
            <button id="login-btn" type="button" onClick={handleLogin} className="bg-green-400 rounded-lg h-fit w-fit 2xl:py-3 py-2 px-10">
              Login
            </button>
            <button id="signup-btn" type="button" onClick={handleSignUp} className="bg-blue-400 rounded-lg h-fit w-fit 2xl:py-3 py-2 px-10">Sign-up</button>

        </footer>
      </article>
    </div>
  );
}

export default Login;
