import configData from "../config.json";

interface AlternativesTaskModel {
    questions: AlternativesQuestionModel[];
  }

interface AlternativesQuestionModel {
    question: AlternativesQuestion;
    answers: AlternativesAnswer[];
    savedPickedAnswer: AlternativesAnswer | null;
  }
  
  interface AlternativesQuestion {
    text: string;
  }
  
  interface AlternativesAnswer {
    text: string;
    isCorrect: boolean;
    incorrectHint: string;
  }

interface ATaskAttemptData {
    text: string;
    savedPickedAnswer: AlternativesAnswer;
}

interface AlternativesAnswerAPIModel {
    answer_text: string;
    is_correct: boolean;
    incorrect_hint: string;
}

interface AlternativeQuestionAPIModel {
    question_text: string;
    difficulty: number;
    concept: number;
    answers: AlternativesAnswerAPIModel[];
}
interface AlternativeTaskAPIModel {
    questions: AlternativeQuestionAPIModel[];
}
  
export async function GetTask(id: number ): Promise<AlternativesTaskModel> {
    return fetch(`${configData.SERVER_URL}/alternativeTasks/${id}/`, 
    {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
        },
    }
    )
    .then( (response) => response.json())
    .then((data) => {

        

        const task: AlternativesTaskModel = {
            questions: data.questions.map((question: any) =>{
                return {
                    question: {
                        text: question.question_text? question.question_text : "",
                    },
                    answers: question.answers.map((answer: any) => {
                        return {
                            text: answer.answer_text? answer.answer_text : "",
                            isCorrect: answer.is_correct? answer.is_correct : false,
                            incorrectHint: answer.incorrect_hint? answer.incorrect_hint : "",
                        }

                    }
                    ),
                    savedPickedAnswer: null,
                }
            }),
        }

        return task;

    })
    .catch((error)=> {
        console.log(error);
        throw error; // Rethrow the error to be caught by the caller
    });
}

export async function SendAttempt( studentId: number, taskId: number, taskData: ATaskAttemptData[] ): Promise<any> {
    return fetch(`${configData.SERVER_URL}/alternativeTaskAttempt/`, 
    {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({
            student: studentId,
            task: taskId,
            task_data: taskData,
        }),
    })
    .then((response)=> response.json())
    .then(
        (data) => {
            console.log(data);
        }
    )
    .catch((error)=> {
        console.log(error);
        throw error; // Rethrow the error to be caught by the caller
    }
    );
}

