import configData from "../config.json";


export async function GetMainDashboard(): Promise<any> {
    return fetch(`${configData.SERVER_URL}/dashboard/main/`,
    {
        method: "GET",
        headers: {
            "Content-Type": "application/json",
        },
    }
    ).then((response)=> response.json())
    .then((data) => {
        console.log(data);
        return data;
    })
    .catch((error) => {
        console.log(error);
        throw error;
    })
}

export async function GetStudentDashboard(id: number): Promise<any> {
    return fetch(`${configData.SERVER_URL}/dashboard/student/`,
    {
        method: "POST",
        headers: {
            "Content-Type": "application/json",
        },
        body: JSON.stringify({
            student_id: id,
        }),
    }
    ).then((response)=> response.json())
    .then((data) => {
        console.log(data);
        return data;
    })
    .catch((error) => {
        console.log(error);
        throw error;
    })

}