import React from "react";
import "./App.css";
import { useState } from "react";
import Home from "./components/Home";
import TeacherHome from "./components/TeacherHome";
import AlternativesTask from "./components/Alternatives/AlternativesTask";
import CalculusTask from "./components/Calculus/CalculusTask";
import Login from "./components/Login";
import CreateATask from "./components/Alternatives/CreateATask";
import MainDashboard from "./components/Dashboards/MainDashboard";
import StudentDashboard from "./components/Dashboards/StudentDashboard";

interface currentTask {
  type: string;
  id: number;
}

function App() {
  const [currentTask, setcurrentTask] = useState<currentTask>({
    type: "",
    id: 0,
  });
  const [currentView, setCurrentView] = useState<{ view: string, taskId?: number }>({ view: "login" });
  const [currentUserId, setCurrentUserId] = useState(0);


  return (
    <>
      { currentView.view === "login" ? (
        <Login 
          setCurrentView={setCurrentView}
          setCurrentUserId={setCurrentUserId}
        />
      ) :
      currentView.view === "home" ? (
        <Home
          setCurrentView={setCurrentView}
          setCurrentTask={setcurrentTask}
          currentTask={currentTask}
          currentUserId={currentUserId}
        />
      ):
      currentView.view === "teacherHome" ? (
        <TeacherHome
          setCurrentView={setCurrentView}
          currentUserId={currentUserId}
        />
      ):
      currentView.view === "createATask" ? (
        <CreateATask
          setCurrentView={setCurrentView}
        />
      )
       : currentView.view === "alternativesTask" && currentView.taskId ? (
        <AlternativesTask
          setCurrentView={setCurrentView}
          taskId={currentView.taskId}
          studentId={currentUserId}
        />
      ) : currentView.view === "calculusTask" && currentView.taskId ? (
        <CalculusTask
          setCurrentView={setCurrentView}
          taskId={currentView.taskId}
          studentId={currentUserId}
        />
      ) : 
      currentView.view === "studentDashboard" ? (
        <StudentDashboard
          setCurrentView={setCurrentView}
        />
      ) :

      currentView.view === "mainDashboard" ? (
        <MainDashboard
          setCurrentView={setCurrentView}

        />
      ) :
      (
        <h1>Not Found</h1>
      )}
    </>
  );
}

export default App;
