import { useState } from "react";
import { AlternativesQuestionModel, AlternativesAnswer } from "./Interfaces";
import { CreateTask } from "../../services/AlternativeTaskService";
interface view {
  view: string;
  taskId?: number;
}

interface CreateATaskProps {
  setCurrentView: (view: view) => void;
}

interface AlternativeQuestionAPIModel {
  question_text: string;
  difficulty: number;
  concept: number;
  answers: AlternativesAnswerAPIModel[];
}

interface AlternativesAnswerAPIModel {
  answer_text: string;
  is_correct: boolean;
  incorrect_hint: string;
}

function CreateATask({ setCurrentView }: CreateATaskProps) {
  const [questions, setQuestions] = useState([
    {
      question: {
        text: "",
        difficulty: 0,
        concept: 1,
      },
      answers: [
        { text: "", isCorrect: false, incorrectHint: "" },
        { text: "", isCorrect: false, incorrectHint: "" },
        { text: "", isCorrect: false, incorrectHint: "" },
      ],
    },
  ]);

  const [toBeDeletedIndex, setToBeDeletedIndex] = useState<number | null>(null);

  const handleGoBack = () => {
    setCurrentView({ view: "teacherHome" });
  };

  const handleAddQuestion = () => {
    const newQuestion = {
      question: {
        text: "",
        difficulty: 1,
        concept: 1,
      },
      answers: [
        { text: "", isCorrect: false, incorrectHint: "" },
        { text: "", isCorrect: false, incorrectHint: "" },
        { text: "", isCorrect: false, incorrectHint: "" },
      ],
    };
    setQuestions([...questions, newQuestion]);
  };

  const handleDeleteQuestion = (index: number) => {
    setToBeDeletedIndex(index); // Trigger the animation
    setTimeout(() => {
      const updatedQuestions = [...questions];
      updatedQuestions.splice(index, 1);
      setQuestions(updatedQuestions);
      setToBeDeletedIndex(null);  // Reset to null
    }, 260);  
  };

  const handleQuestionTextChange = (index: number, text: string) => {
    const updatedQuestions = [...questions];
    updatedQuestions[index].question.text = text;
    setQuestions(updatedQuestions);
  };

  const handleAnswerTextChange = (
    questionIndex: number,
    answerIndex: number,
    text: string
  ) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].answers[answerIndex].text = text;
    setQuestions(updatedQuestions);
  };

  const handleAnswerCorrectChange = (
    questionIndex: number,
    answerIndex: number
  ) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].answers[answerIndex].isCorrect =
      !updatedQuestions[questionIndex].answers[answerIndex].isCorrect;
    setQuestions(updatedQuestions);
  };

  const handleAnswerHintChange = (
    questionIndex: number,
    answerIndex: number,
    hint: string
  ) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].answers[answerIndex].incorrectHint = hint;
    setQuestions(updatedQuestions);
  };

  const handleDifficultyChange = ( questionIndex: number, difficulty: number) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].question.difficulty = difficulty;
    setQuestions(updatedQuestions);
  };

  const handleConceptChange = ( questionIndex: number, concept: number) => {
    const updatedQuestions = [...questions];
    updatedQuestions[questionIndex].question.concept = concept;
    setQuestions(updatedQuestions);
  }

  const handleSaveTask = () => {
    // Handle saving the task data to the server
    if (questions.length === 0) {
        console.log("No questions provided");
        console.log("No answer picked");

      document.getElementById("save-btn")?.classList.add("animate-wiggle");
      document.getElementById("save-btn")?.classList.add("bg-red-500");
      setTimeout(() => {
        document.getElementById("save-btn")?.classList.remove("animate-wiggle");
        document.getElementById("save-btn")?.classList.remove("bg-red-500");
        // console.log("Wiggle removed")
      }, 500);
      return;
    }
    
    document.getElementById("save-btn")?.classList.add("animate-bounce");
    // Create a new task object to send to the server
    const parsedQuestions = questions.map((question) => {
      const parsedQuestion: AlternativeQuestionAPIModel = {
        question_text: question.question.text,
        difficulty: question.question.difficulty,
        concept: question.question.concept,
        answers: question.answers.map((answer) => {
          return {
            answer_text: answer.text,
            is_correct: answer.isCorrect,
            incorrect_hint: answer.incorrectHint,
          };
        }),
      };
      return parsedQuestion;
    }
    );
    const task = {
      questions: parsedQuestions,
    };

    CreateTask(task).then((response) => {
      console.log("Task saved:", response);
      setCurrentView({ view: "teacherHome" });
      document.getElementById("save-btn")?.classList.remove("animate-bounce");

    }
    ).catch((error) => {
      document.getElementById("save-btn")?.classList.remove("animate-bounce");
      console.log(error);
    }
    );
    
  };

  return (
    <div className="h-screen">
      <header className="w-full h-[10%] bg-green-200 flex flex-row-reverse px-28 content-center">
        <button
          type="button"
          className="bg-slate-400 h-min self-center px-4 py-2 rounded-md text-white font-bold text-4xl"
          onClick={handleGoBack}
        >
          🏠
        </button>
      </header>
      <section className=" bg-slate-500 h-[80%] px-[15%] py-5 overflow-y-scroll">
        {questions.map((question, questionIndex) => (
          <div
            key={questionIndex}
            id={`question-${questionIndex}`}
            className={`mb-5 ${
                toBeDeletedIndex === questionIndex
                  ? 'animate-zoom-delete'
                  : questionIndex % 2 === 0
                  ? 'animate-fade-in-left'
                  : 'animate-fade-in-right'
              }`}
          >
          <header className="flex justify-between">
            <select
              aria-label="Difficulty"
              value={question.question.difficulty}
              onChange={(e) =>
                handleDifficultyChange(questionIndex, Number(e.target.value))
              }
              className="block w-1/4 text-lg p-2 mb-2 rounded text-center placeholder:text-slate-500  text-black bg-green-300 shadow-md"
            >
              <option value={1}>Easy</option>
              <option value={2}>Medium</option>
              <option value={3}>Hard</option>
            </select>
            <select
              aria-label="Concept"
              value={question.question.concept}
              onChange={(e) =>
                handleConceptChange(questionIndex, Number(e.target.value))
              }
              className="block w-1/4 text-lg p-2 mb-2 rounded text-center placeholder:text-slate-500  text-black bg-green-300 shadow-md"
            >
              <option value={1}>Tipos de Ondas</option>
              <option value={2}>Caracteristicas de las Ondas</option>
              <option value={3}>Ondas Estacionarias y Viajeras</option>
              <option value={4}>Movimiento de una Cuerda</option>
              <option value={5}>Ondas Sonoras</option>
            </select>
          </header>
            <textarea
              value={question.question.text}
              onChange={(e) =>
                handleQuestionTextChange(questionIndex, e.target.value)
              }
              placeholder="Question Text"
              className="block w-full text-lg p-2 mb-2 rounded text-center placeholder:text-slate-500  text-black bg-green-300 shadow-md"
            />
            <div className="grid grid-cols-3 gap-2 mb-1">
              {question.answers.map((answer, answerIndex) => (
                <div key={answerIndex} className="relative">
                  <input
                    type="text"
                    value={answer.text}
                    onChange={(e) =>
                      handleAnswerTextChange(
                        questionIndex,
                        answerIndex,
                        e.target.value
                      )
                    }
                    placeholder={`Answer ${answerIndex + 1}`}
                    className="block h-24 w-full p-2 shadow-md bg-blue-500 placeholder:text-slate-700 rounded text-black text-center"
                  />
                  <label className="absolute top-0 right-1 ml-2 mt-1">
                    <input
                      type="checkbox"
                      checked={answer.isCorrect}
                      onChange={() =>
                        handleAnswerCorrectChange(questionIndex, answerIndex)
                      }
                      className="checked:bg-green-500 checked:border-transparent"
                    />
                    Correct
                  </label>
                  <textarea
                    value={answer.incorrectHint}
                    onChange={(e) =>
                      handleAnswerHintChange(
                        questionIndex,
                        answerIndex,
                        e.target.value
                      )
                    }
                    placeholder="Incorrect Hint"
                    className="block w-full p-2 mt-2  placeholder:text-slate-700 bg-red-500 shadow-sm"
                  />
                </div>
              ))}
            </div>
            <button
              onClick={() => handleDeleteQuestion(questionIndex)}
              className="bg-red-500 rounded-md px-2 py-3 text-white hover:text-red-800"
            >
              Borrar pregunta
            </button>
          </div>
        ))}
        <button
          onClick={handleAddQuestion}
          className="bg-green-600 rounded-md px-2 py-3 self-end text-white hover:text-green-800"
        >
          Añadir pregunta
        </button>
      </section>
      <footer className="h-[5%] bg-green-200 grid grid-cols-4">
        <div className="col-span-3"></div>
        <div className="col-span-1 flex flex-row-reverse gap-8 pt-3 px-28 content-center">
          <button
          id="save-btn"
            onClick={handleSaveTask}
            className="bg-slate-400 h-min min-w-min self-center 2xl:px-4 px-8 2xl:py-2 w-1/2 rounded-md text-white font-bold"
          >
            Save Task
          </button>
        </div>
      </footer>
      <nav className="h-[5%] bg-green-200 text-center align-text-bottom">
        ______
      </nav>
    </div>
  );
}

export default CreateATask;
